<template>
  <v-container fluid class="col-12 my-3">
    <v-row>
      <v-card width="90%" class="mx-auto" elevation="10">
         <v-img @click="goHomeStore" class="img-store mx-auto" max-width="200" max-height="150" :aspect-ratio="1.7" :src="data.image" alt="image_store"></v-img>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title class="text--store">{{ data.name}}</v-list-item-title>
            <v-list-item-subtitle class="text--price">Horario: {{ dataStore.opening_time }} - {{ dataStore.closing_time }}</v-list-item-subtitle>
            <v-list-item-subtitle class="text--description">{{ data.description }}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
        <v-card-actions>
          <search></search>
        </v-card-actions>
      </v-card>
    </v-row>
    <v-row>
      <v-col cols="11" class="mx-auto d-lg-none d-xl-flex">
        <mobile-categories></mobile-categories>
      </v-col>
      <v-col class="ml-auto d-none d-lg-flex d-xl-none" cols="3">
        <store-categories></store-categories>
      </v-col>
      <v-col class="mr-auto" cols="12" xl="8" lg="8">
        <router-view @reloadData="reloadData()"></router-view>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Categories from "@/components/Products/Categories";
import StoreContent from "@/components/Products/StoreContent";
import CategoriesMobile from "@/components/Products/CategoriesMobile";
import SearchBar from "@/components/Products/SearchInStore"
import { mapActions, mapState, mapGetters } from "vuex";

export default {
  name: "Stores",
  components: {
    StoreCategories: Categories,
    ContentStore: StoreContent,
    MobileCategories: CategoriesMobile,
    Search: SearchBar
  },
  computed: {
    ...mapState("products/stores", ["data"]),
    ...mapGetters("login", ["dataStore"])
  },
  mounted() {
    this.reloadData()
  },
  data() {
    return {
      storeIdentifier: this.$route.params.id
    };
  },
  methods: {
    ...mapActions("products/stores", [
      "getStoreData",
      "getFeaturedProductsStore"
    ]),
    goHomeStore() {
      if (this.$route.params.id) {
        const id = this.$route.params.id;
        this.$router.push({ name: "store", params: { id } }).catch(err => err);
      }
    },
    reloadData() {
      if (this.$route.params.id) {
      this.getStoreData(this.$route.params.id);
      this.getFeaturedProductsStore(this.$route.params.id);
    }
    }
  }
};
</script>

