<template>
  <v-card class="mx-auto" elevation="10">
    <v-subheader class="text--description">Categorías</v-subheader>
    <v-chip-group v-if="!subCategorySelected" :column="true" class="px-2">
      <v-chip
        label
        style="text-transform: lowercase"
        color="primary"
        text-color="white"
        v-for="tag in categories"
        :key="tag.id"
        @click="filterSubcategory(tag)"
      >{{ tag.name }}</v-chip>
    </v-chip-group>
    <v-chip
      class="ma-1"
      v-if="subCategorySelected"
      label
      style="text-transform: lowercase"
      color="primary"
      text-color="white"
      close
      @click:close="clearSubCategory"
    >{{ categorySelected }}</v-chip>
    <v-chip-group
      v-if="subCategorySelected"
      active-class="primary--text"
      :column="true"
      class="px-2"
    >
      <v-chip
        label
        style="text-transform: lowercase"
        v-for="tag in subcategories"
        :key="tag.id"
        @click="goToCategory(tag.id, tag.name)"
      >{{ tag.name }}</v-chip>
    </v-chip-group>
  </v-card>
</template>
<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState("products/stores", ["categories"])
  },
  data() {
    return {
      subCategorySelected: false,
      categorySelected: "",
      subcategories: []
    };
  },
  methods: {
    goToCategory(id, name) {
      this.$router
        .push({ name: "category", query: { cat: id, name: name } })
        .catch(err => err);
    },
    filterSubcategory(category) {
      if (category.subcategories.length > 0) {
        this.subCategorySelected = true;
        this.categorySelected = category.name;
        this.subcategories = category.subcategories;
      } else {
        this.subCategorySelected = false;
        this.categorySelected = "";
        this.subcategories = [];
      }
    },
    clearSubCategory() {
        this.subCategorySelected = false;
        this.categorySelected = "";
        this.subcategories = [];
    }
  }
};
</script>

<style>
.v-list-group__items .v-list-item {
  padding-left: 20px !important;
}
.title_categories {
  text-transform: capitalize;
  white-space: normal;
}
</style>