<template>
        <v-col class="mx-auto" cols="12" lg="5" md="7" sm="7">
          <v-autocomplete
            :height="50"
            v-model="model"
            :loading="isLoading"
            :search-input.sync="search"
            @keyup.enter="submit($event, search)"
            outlined
            :cache-items="false"
            clearable
            hide-details
            hide-selected
            hide-no-data
            label="Busca cualquier producto..."
            append-icon
            prepend-inner-icon="mdi-magnify"
            solo
          >
            <!-- :items="dataResultsSearchByStore"
                item-text="name"
            item-value="id"-->
            <template v-slot:item="{ item }">
              <v-list-item-content>
                <v-list-item-title v-text="item.name"></v-list-item-title>
              </v-list-item-content>
              <v-list-item-icon>
                <v-icon>mdi-chevron-right</v-icon>
              </v-list-item-icon>
            </template>
          </v-autocomplete>
        </v-col>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "Search",
  computed: {
    ...mapState("products", ["dataResultsSearchByStore"])
  },
  data() {
    return {
      isLoading: false,
      model: null,
      search: null
    };
  },
  methods: {
    ...mapActions("products", ["getProductsByStoreSearch"]),
    submit(event) {
      event.target.blur();
      this.$router.push({ name: "storeResults", query: {input_search: this.search} }).catch(err => err);
    }
  }
};
</script>
