<template>
  <v-card class="scrollCard" min-height="80vh" max-height="80vh" min-width="100%" elevation="10">
    <v-list>
      <v-subheader class="text--description">Categorías</v-subheader>
      <v-list-group v-for="item in categories" :key="item.id" no-action>
        <template v-slot:activator>
          <v-list-item-content>
            <v-list-item-title class="title_categories" v-text="item.name"></v-list-item-title>
          </v-list-item-content>
        </template>

        <v-list-item v-for="subItem in item.subcategories" :key="subItem.id" @click="goToCategory(subItem.id, subItem.name)">
          <v-list-item-content>
            <v-list-item-title class="title_categories" v-text="subItem.name"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-group>
    </v-list>
  </v-card>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState("products/stores", ["categories"])
  },
  data() {
    return {};
  },
  methods: {
    goToCategory(id, name) {
       this.$router.push({ name: "category", query: {cat: id, name: name} }).catch(err => err);
    }
  },
};
</script>

<style>
.v-list-group__items .v-list-item {
  padding-left: 20px !important;
}
.title_categories {
    text-transform: capitalize;
    white-space: normal;
}
.scrollCard {
  overflow-y: auto;
}
</style>